
.vue-list{
  width: 100%;
  height: 100%;
  overflow-y: auto;
}
.vue-list::scroll-bar {
  width: 0;
}
.vue-list ul {
  margin: 0;
  padding: 0;
}
.vue-list li {
  text-decoration: none;
  height: 44px;
  font-size: 14px;
  line-height: 3;
  text-align: left;
  padding-left: 15px;
  border-bottom: 1px solid #ddd;
  box-sizing: border-box;
  background: #fff;
}
.vue-list li.line-top,
.vue-list li.line-bottom {
  border-bottom: 0;
}
.load-more-gif{
  width: 100%;
  height: 44px;
  text-align: center;
  line-height: 44px;
  background: #fff;
  border-top: none;
  color: #48B884;
}



